document.addEventListener('DOMContentLoaded',function(){
    scrollNav();
    navegacionFija();
});


function navegacionFija(){

    const barra = document.querySelector('.header');

    // Registrar Intersection Observer
    const observer = new IntersectionObserver(function(entries){
       if(entries[0].isIntersecting){
           barra.classList.remove('fijo');
       }else{
           barra.classList.add('fijo');
       }
    });

    //Elemento Observar
    observer.observe(document.querySelector('.sobre-festival'));
}

/*
Intersection Observer
    2 partes 
        > Elemento a observar
        > Registrar Intersection Observer
    entries -> informacion del elemento a observar
*/

function scrollNav(){
    const enlaces = document.querySelectorAll('.navegacion-principal a');
    enlaces.forEach(function(enlace){
        enlace.addEventListener('click',function(evento){
            evento.preventDefault();
            const seccion = document.querySelector(evento.target.attributes.href.value);
            seccion.scrollIntoView({
                behavior: 'smooth'
            });
        })
    })
}

/*
No se puede agregar un eventListener a una coleccion de elementos
se debe iterar cada elemento y agregar el evento a cada elemento por separado
*/